import React from "react";
import styled from "styled-components";
import { graphql } from "gatsby";

const TagExplorerWrapper = styled.div`
  position: absolute;
  right: 15px;
  top: 50%;
  height: 430px;
  width: 200px;
`;

const TagExplorer = ({ group }) => {
  return (
    <TagExplorerWrapper>
      <h4>Tags</h4>
      {
        group.map(item => (
        <span>#{item.tag} - {item.totalCount}</span>
        ))
      }
    </TagExplorerWrapper>
  );
};

export default TagExplorer;

export const pageQuery = graphql`
  query {
    allMarkdownRemark {
      group(field: frontmatter___tags) {
        tag: fieldValue
        totalCount
      }
    }
  }
`;
